export default "import React from 'react';\nimport Grid from '@material-ui/core/Grid';\nimport Button from '@material-ui/core/Button';\nimport ButtonGroup from '@material-ui/core/ButtonGroup';\nimport Box from '@material-ui/core/Box';\n\nexport default function GroupedButtons() {\n  return (\n    <Grid container spacing={3}>\n      <Grid item xs={12} md={6}>\n        <Grid container spacing={1} direction='column' alignItems='center'>\n          <Grid item>\n            <ButtonGroup size='small' aria-label='small outlined button group'>\n              <Button>One</Button>\n              <Button>Two</Button>\n              <Button>Three</Button>\n            </ButtonGroup>\n          </Grid>\n          <Grid item>\n            <ButtonGroup\n              color='primary'\n              aria-label='outlined primary button group'>\n              <Button>One</Button>\n              <Button>Two</Button>\n              <Button>Three</Button>\n            </ButtonGroup>\n          </Grid>\n          <Grid item>\n            <ButtonGroup\n              color='secondary'\n              size='large'\n              aria-label='large outlined secondary button group'>\n              <Button>One</Button>\n              <Button>Two</Button>\n              <Button>Three</Button>\n            </ButtonGroup>\n          </Grid>\n        </Grid>\n      </Grid>\n      <Grid item xs={12} md={6}>\n        <Grid container spacing={1} direction='column' alignItems='center'>\n          <Grid item>\n            <Box boxShadow={0} clone>\n              <ButtonGroup\n                variant='contained'\n                size='small'\n                aria-label='small contained button group'>\n                <Button>One</Button>\n                <Button>Two</Button>\n                <Button>Three</Button>\n              </ButtonGroup>\n            </Box>\n          </Grid>\n          <Grid item>\n            <Box boxShadow={0} clone>\n              <ButtonGroup\n                variant='contained'\n                color='primary'\n                aria-label='full-width contained primary button group'>\n                <Button>One</Button>\n                <Button>Two</Button>\n                <Button>Three</Button>\n              </ButtonGroup>\n            </Box>\n          </Grid>\n          <Grid item>\n            <Box boxShadow={0} clone>\n              <ButtonGroup\n                variant='contained'\n                color='secondary'\n                size='large'\n                aria-label='large contained secondary button group'>\n                <Button>One</Button>\n                <Button>Two</Button>\n                <Button>Three</Button>\n              </ButtonGroup>\n            </Box>\n          </Grid>\n        </Grid>\n      </Grid>\n      <Grid item xs={12}>\n        <ButtonGroup fullWidth aria-label='full width outlined button group'>\n          <Button>Full</Button>\n          <Button>width</Button>\n          <Button>ButtonGroup</Button>\n        </ButtonGroup>\n      </Grid>\n    </Grid>\n  );\n}\n";